type Props = {
  publicKey: web3.PublicKey;
  isSol?: boolean;
  className?: string;
};
export const QRAddressWidget: FunctionComponent<Props> = ({
  publicKey,
  isSol,
  className
}) => {
  const publicKeyBase58 = publicKey.toBase58();
  const {
    name
  } = useTokenInfo(publicKeyBase58);

  const handleCopyClick = () => {
    try {
      void navigator.clipboard.writeText(publicKeyBase58);
    } catch (error) {
      console.error(error);
    }
  }; // function handleShareClick() {
  //   navigator.share({ text, title, url });
  // }


  return <WrapperCard className={className}>
      <HeaderWrapper>
        <TokenWrapper>
          <TokenName>{name} Address</TokenName>
          <TokenAddress>{publicKeyBase58}</TokenAddress>
        </TokenWrapper>
        <ShareWrapper onClick={handleCopyClick}>
          <ShareIcon name="copy" />
        </ShareWrapper>
      </HeaderWrapper>
      <Content>
        <QRcode value={publicKeyBase58} size={148} />
        {
        /* <ActionsWrapper> */
      }
        {
        /*  <Action>Copy image</Action> */
      }
        {
        /*  <Action>Share</Action> */
      }
        {
        /* </ActionsWrapper> */
      }
      </Content>
    </WrapperCard>;
};