type Props = {
  tokenPublicKey: string;
  tokenAmount: string;
  onTokenChange: (tokenPublicKey: string) => void;
  onAmountChange: (tokenAmount: string) => void;
  disabled?: boolean;
};
export const FromSelectInput: FunctionComponent<Props> = ({
  tokenPublicKey,
  tokenAmount,
  onTokenChange,
  onAmountChange,
  disabled
}) => {
  const dispatch = useDispatch();
  const selectorRef = useRef<HTMLDivElement>(null);
  const [isOpen, setIsOpen] = useState(false);
  const order = useSelector((state: RootState) => state.entities.tokens.order);
  const entrypoint = useSelector((state: RootState) => state.data.blockchain.entrypoint);
  const publicKey = useSelector((state: RootState) => state.data.blockchain.account?.publicKey.toBase58());
  const preparedOrder = useMemo(() => publicKey ? [publicKey, ...order] : order, [publicKey, order]);
  const {
    name,
    mint,
    symbol,
    amount
  } = useTokenInfo(tokenPublicKey);
  useEffect(() => {
    dispatch(getOwnedTokenAccounts());
  }, [entrypoint]);

  const handleAwayClick = (e: MouseEvent) => {
    if (!selectorRef.current?.contains((e.target as HTMLDivElement))) {
      setIsOpen(false);
    }
  };

  useEffect(() => {
    window.addEventListener('click', handleAwayClick);
    return () => {
      window.removeEventListener('click', handleAwayClick);
    };
  }, []);

  const handleSelectorClick = () => {
    if (!preparedOrder) {
      return;
    }

    setIsOpen(!isOpen);
  };

  const handleItemClick = (nextPublicKey: string) => {
    setIsOpen(false);
    onTokenChange(nextPublicKey);
  };

  const handleAllBalanceClick = () => {
    onAmountChange(String(amount));
  };

  const handleAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.trim();
    onAmountChange(value);
  };

  return <Wrapper>
      <TopWrapper>
        <FromTitle>From</FromTitle>
        <AllBalance onClick={handleAllBalanceClick} className={classNames({
        disabled
      })}>
          Use all balance
        </AllBalance>
      </TopWrapper>
      <MainWrapper>
        <TokenAvatarWrapper>
          <TokenAvatar mint={mint} size={44} />
        </TokenAvatarWrapper>
        <InfoWrapper>
          <SpecifyTokenWrapper>
            <TokenWrapper ref={selectorRef} onClick={handleSelectorClick} className={classNames({
            disabled
          })}>
              <TokenName title={tokenPublicKey}>
                {name || symbol || shortAddress(tokenPublicKey)}
              </TokenName>
              <ChevronWrapper>
                <ChevronIcon name="arrow-triangle" />
              </ChevronWrapper>
            </TokenWrapper>
            <AmountInput placeholder="0" value={tokenAmount} onChange={handleAmountChange} disabled={disabled} />
          </SpecifyTokenWrapper>
          <BalanceWrapper>
            <BalanceText>
              Balance = {amount} {symbol}
            </BalanceText>
            <BalanceText>
              = <AmountUSDTStyled value={tokenAmount} symbol={symbol} />
            </BalanceText>
          </BalanceWrapper>
        </InfoWrapper>
      </MainWrapper>
      {isOpen ? <DropDownListContainer>
          <DropDownHeader>Your wallets</DropDownHeader>
          <DropDownList>
            {preparedOrder.map(publicKey => <TokenRow key={publicKey} publicKey={publicKey} onClick={handleItemClick} />)}
          </DropDownList>
        </DropDownListContainer> : undefined}
    </Wrapper>;
};